<template>
  <div style="width: calc(100vw - 220px); padding: 10px;">
    <div style="display: flex; justify-content: space-between;">
      <div style="display: flex;">
        <el-input
          style="border-radius: 4px;"
          placeholder="Search"
          prefix-icon="el-icon-search"
          v-model="searchKeyword"
        ></el-input>

        <el-select
          v-model="statusType"
          placeholder="Select"
          style="margin-left: 10px"
        >
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </div>
      <el-button
        style="background: #0f3959; color: white;"
        icon="el-icon-plus"
        @click="openNewIconDialog"
        >New Icons</el-button
      >
    </div>

    <el-table
      :data="tableData"
      border
      style="width: 100%; margin-top: 10px;"
      :header-cell-style="headerCellStyle"
      @row-click="onClickRow"
      height="700"
    >
      <el-table-column sortable label="Icons" width="100">
        <template slot-scope="scope">
          <img :src="scope.row.icon" alt="" height="20px" />
        </template>
      </el-table-column>
      <el-table-column sortable prop="status" label="Status" width="180">
      </el-table-column>
      <el-table-column sortable prop="applicationName" label="Application Name">
      </el-table-column>
      <el-table-column prop="filePath" label="File Path"> </el-table-column>
    </el-table>
  </div>
</template>

<script>
import PowerPointIcon from "../assets/image/powerpoint.png";
export default {
  data() {
    return {
      searchKeyword: "",
      options: [
        {
          value: "",
          label: "Show All",
        },
        {
          value: "active",
          label: "Active",
        },
        {
          value: "deactive",
          label: "Deactivate",
        },
      ],
      statusType: "",
      tableData: [],
      powerPointIcon: PowerPointIcon,
    };
  },
  methods: {
    moveToEditGroup() {
      this.$router.push("/new-group");
    },
    headerCellStyle() {
      return {
        background: "#0f3959",
        color: "white",
        fontWeight: "normal",
      };
    },
    openNewIconDialog() {
      this.$store.commit("setNewIconDialogVisibility", true);
    },
    onClickRow(object) {
      console.log(object);
      this.$store.commit("setEditIconDialogVisibility", true);
    },
  },
  mounted() {
    // generate dummy tableData
    for (var i = 1; i <= 10; i++) {
      this.tableData.push({
        icon: this.powerPointIcon,
        status: i % 2 == 0 ? "Active" : "Deactivate",
        applicationName: `Application - ${i}`,
        filePath: `Path - ${i}`,
      });
    }
  },
};
</script>

<style lang="scss" scoped></style>
